*, *::before, *::after {
  box-sizing: inherit;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: normal normal normal 1rem/1.6 -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem; 
}

body {
    color: #404040;
    background: white;
    font-size: 1rem;
}

p, ol, ul, dl, table {
    margin: 0 0 1.5rem 0;
}

h1, h2, h3, h4, h5 {
    margin: 1.5rem 0;
    font-weight: 600;
    font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
    line-height: 1.2;
    color: #404040;
}

h3 {
  font-size: 1.25rem; }

.container {
    max-width: 70%;
    padding: 0 1rem;
    margin-left: auto;
    margin-right: auto;
}

.parentContainer{
  display: inline-block;
  max-width: 35%;
}
.ChildContainer{
  display: inline-block;
  max-width: 70%;
  padding-left: 2.5rem;
}

.content-section {
  padding: 30px 0; 
}

.submitForm{
  background-color: #e6e6e6;
  text-align: center;
  padding: 1rem 0rem;
}

.button, a.button, button, [type=submit], [type=reset], [type=button] {
  -webkit-appearance: none;
  display: inline-block;
  border: 1px solid #15bd00;
  background: #15bd00;
  color: #ffffff;
  font-weight: 600;
  font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  text-transform: none;
  padding: .75rem 1.25rem;
  margin: 0 0 0 1rem;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  line-height: 1; 
}

button:hover, [type=submit]:hover, [type=reset]:hover, [type=button]:hover {
  border: 1px solid #119c00;
  background: #119c00;
  color: #ffffff;
  text-decoration: none; 
}

button:focus, button:active, [type=submit]:focus {
  border: 1px solid #119c00;
  background: #119c00;
  color: #ffffff;
  text-decoration: none; 
}

.button::-moz-focus-inner, a.button::-moz-focus-inner, button::-moz-focus-inner, [type=submit]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=button]::-moz-focus-inner {
  border: 0;
  padding: 0; 
}

.removeBtn{
  color: #dc3545;
  margin: 0 .5rem;
}

[type=text], select, textarea {
    display: inline-block;
    border: 1px solid #dedede;
    border-radius: 4px;
    padding: .75rem;
    outline: none;
    background: white;
    font-size: 1rem;
    width: 50%;
    max-width: 100%;
    line-height: 1;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;
	max-width: 100%;
}

.rowLine{
  background-color: #aaffaa;
}

.rowLine:nth-child(even) {
  background-color: #caffca;
}

thead th {
    border-bottom: 2px solid #dedede;
}

td {
    border-bottom: 1px solid #dedede;
}

th, td {
    text-align: left;
    padding: .5rem;
}

.tdRemoveBtn{
  text-align: right;
  padding-right: 1rem;
}

@media (max-width: 600px) {
  .container {
    max-width: 100%;
}
  .content-section {
    padding: 60px 0; 
  }
  h3 {
    font-size: 1.75rem; 
  }

}